  /* Timeline */
.timeline {
  border-left: 2px solid #444444;
  color: #9e9e9e;
  letter-spacing: 0.5px;
  position: relative;
  line-height: 1.4em;
  padding: 50px;
  text-align: left;
  font-weight: 100;
  max-width: 480px;
  left: 80px;
  margin-bottom: 20px;
  margin-left: 80px;
}
.timeline .event {
  margin-bottom: 55px;
  position: relative;
}

.timeline .event:last-of-type {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none;
}
.timeline .event:before,
.timeline .event:after {
  position: absolute;
  display: block;
  top: 0;
}
.timeline .event:before {
  left: -217.5px;
  color: darkgrey;
  content: attr(data-date);
  text-align: right;
  font-weight: bold;
  font-size: 0.9em;
  min-width: 120px;
}
.timeline .event:after {
  box-shadow: 0 0 0 3px #f22953;
  opacity: 0.9;
  left: -56.5px;
  background: black;
  border-radius: 50%;
  height: 11px;
  width: 11px;
  content: "";
  top: 5px;
}
/********************* Timeline from https://codepen.io/darcyvoutt/pen/ogPrpK *******************************************************/

.main-content{
  font-size: 14px;
}

.sub-content{
  font-size: 12px;
  color: darkgrey;
}

.under-dotted-line{
  animation: dot 1.5s infinite;
}

@keyframes dot {
  from{
    border-bottom: 3px dotted #212121;
  }
  to{
    border-bottom: 3px dotted #f22953;
  }
}

#hover-text{
  display: none;
  position: absolute;
  right: 80px;
  top: 20px;
  background-color: #444444;
  font-size: 20px;
  border-radius: 25px;
  border: 1px solid whitesmoke;
  padding: 10px 15px;
}

#hover-text > p:first-child{
  margin-bottom: 10px;
}

#hover-text > p:not(p:first-child){
  color: whitesmoke;
}

#skills:hover{
  cursor: help;
}

#skills:hover + #hover-text{
  display: block;
}

/* 스킬게이지 퍼센트 글자 */
#activity-content-right > div > div > div:first-child > p:last-child{
  color: #a0a0a0;
}

#activity-content-right > div > p:not(p:first-child){
  margin-top: 8%;
}

.gauge-card{
  margin-bottom: 3%;
}

.gauge-card-title{
  margin-bottom: 1%;
}

.gauge-outer{
  width: 100%;
  height: 100%;
  border: 2px solid #dc284e;
  border-radius: 20px
}

.gauge-inner{
  height: 70%;
  background-color: #dc284e;
  border-radius: 20px;
  margin-left: 3px;
}

#activity-content-right > div:last-child > p{
  margin-top: 10%;
}

.tech-stack:not(.tech-stack:last-child){
  margin-bottom: 5px;
}

.stack{
  background-color: #cdd7e0;
  color: #212121;
  border-radius: 10px;
  padding: 1%;
  font-size: 14px;
  font-weight: bold;
  z-index: 999;
}

.stack:not(.stack:last-child){
  margin-right: 1%;
}

.techs > .stack:hover{
  cursor: default;
  background-color: #dc284e;
  color: whitesmoke;
}

.tools > .stack:hover{
  cursor: default;
  background-color: #3c55e0;
  color: whitesmoke;
}

.scale, .scale5 {
  transform: scale(1);
  transition: all 0.3s ease-in-out;
}

.scale:hover {
  transform: scale(2);
  z-index: 999;
}

.scale5:hover {
  transform: scale(2.5);
  z-index: 999;
}

.img-award{
  width: 180px;
  height: 250px;
  margin-right: 30px;
  border-radius: 10px;
  background-image: linear-gradient(#fff, #fff),
  linear-gradient(to right, red 0%,  orange 100%);
  background-origin: border-box;
  background-clip: content-box, border-box;
  border: 1px solid transparent;
}

#last-title-before{
  min-width: 1219px;
  margin-top: 75px;
}

#last-title{
  margin-top: 50px;
}

.img-certi{
  width: 150px;
  height: 90px;
  margin-right: 25px;
  border-radius: 10px;
  background-image: linear-gradient(#fff, #fff),
  linear-gradient(to right, red 0%,  orange 100%);
  background-origin: border-box;
  background-clip: content-box, border-box;
  border: 1px solid transparent;
}

#last-div{
  margin-bottom: 100px;
}

#scroll{
  position: fixed;
  bottom: 0;
  left: 50%;
  animation: move 2s infinite;
  z-index: 9999999999999999;
  opacity: 0.7;
}

#scroll > p{
  font-size: 13px;
  text-align: center;
}

#scroll > span{
  font-size: 1.5em;
}

@keyframes move {
  from{
    bottom: 30px;
  }
}

/******************************** end-activity **********************************/


/******************************** start-portfolio **********************************/

.card-wrap{
  margin-bottom: 100px;
  margin-left: 50px;
  height: 33%;
}

.card-wrap:last-child{
  margin-bottom: 0;
}

.portfolio-card{
  transform: scale(1);
  transition: all 0.3s ease-in-out;
  border-radius: 10px;
  background-image: linear-gradient(#444444, #444444),
  linear-gradient(to right, red 0%,  orange 100%);
  background-origin: border-box;
  background-clip: content-box, border-box;
  border: 1px solid transparent;
  min-width: 211px;
  max-width: 300px;
  min-height: 284px;
}

.portfolio-card:hover{
  transform: scale(1.5);
  z-index: 999;
  cursor: pointer;
}

.portfolio-card:not(.portfolio-card:last-child){
  margin-right: 100px;
}

.project-header{
  height: 10%;
  border-bottom: 1px solid whitesmoke;
}

.project-footer{
  height: 10%;
  margin-right: 10px;
}

.project-footer > span{
  animation: bling 1.5s infinite;
  font-size: 10px;
}

@keyframes bling {
  from{
    background: linear-gradient(90deg, #ee0979, #ff6a00);
    -webkit-background-clip: text;
  }
  to {
    background: linear-gradient(90deg, #444444, #444444);
    -webkit-background-clip: text;
  }
 }

.img-in-modal{
  width: 100%;
  height: 50%;
}

.modal-ul{
  margin-top: 10px;
}

.modal-ul > li:hover{
  filter: opacity(0.4) drop-shadow(0 0 0 white);
}

/******************************** end-portfolio **********************************/


